.historySection{
    background-color: rgb(255, 255, 255);
    border-radius:10px;
    box-shadow: 6px 7px 7px rgba(0, 0, 0, 0.25);
    padding: 10px;
}

.purple-border textarea {
    border: 1px solid #58acd2;
}
.purple-border .form-control:focus {
    border: 1px solid #58acd2;
    box-shadow: 0 0 0 0.2rem rgba(111, 196, 248, 0.25);
}